class MyButton extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            items: props.items,
            root: props.root
        }
    }

    handleClick(e) {
        e.preventDefault();
        console.log('The link was clicked.');
    }

    render() {
        return (
            <button onClick={this.handleClick}>up</button>
        );
    }
}

class FileList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            items: props.items,
            root: props.root
        }
    }

    componentDidMount() {
    }

    componentWillUnmount() {

    }

    render() {

        return (
            <ul>
                {
                    this.state.items.map((item, index) => {
                        if (item.isDir) {
                            return <li key={index}><b>{item.text}</b></li>
                        } else {
                            return <li key={index}>{item.text}</li>
                        }

                    })
                }
            </ul>
        );
    }
}

var left_tree = []
var right_tree = []

// is dir // http://stackoverflow.com/questions/15630770/node-js-check-if-path-is-file-or-directory
// arrow function // https://developer.mozilla.org/zh-TW/docs/Web/JavaScript/Reference/Functions/Arrow_functions

fs.readdir(leftFolder, (err, files) => {
    files.forEach(file => {
        var path_string = path.join(leftFolder, file)
        var isDir = fs.lstatSync(path_string).isDirectory()
        left_tree.push({ text: file, id: Date.now(), isDir: isDir })
    })
    ReactDOM.render(
        <FileList items={left_tree} root={rightFolder} />,
        document.getElementById('left_tree')
    )
})
fs.readdir(rightFolder, (err, files) => {
    files.forEach(file => {
        var path_string = path.join(rightFolder, file)
        var isDir = fs.lstatSync(path_string).isDirectory()
        right_tree.push({ text: file, id: Date.now(), isDir: isDir })
    })
    ReactDOM.render(
        <FileList items={right_tree} root={rightFolder} />,
        document.getElementById('right_tree')
    )
})

ReactDOM.render(
    <MyButton />,
    document.getElementById('left_foot')
)
$('#left_head').text(leftFolder)
$('#right_head').text(rightFolder)





